.f {
    display: flex; }
.bg-color {
    background-color: $backgroundPrimaryLight;
    clip-path: url("#h"); }

.bg-color-day {
    background-image: radial-gradient($backgroundPrimaryLight,$backgroundSecondaryLight); }
.bg-color-night {
    background-image: radial-gradient($backgroundPrimaryDark,$backgroundSecondaryDark); }
.full {
    width: 100%;
    height: 100%; }
.half {
    width: auto;
    height: 50%; }
.center {
    justify-content: center;
    align-items: center; }

//container haelt alles Top level
.container {
    display: flex;
    width: 100vw;
    height: 100vh;

    .svglogocon {
        position: absolute;

        .svglogobaivo {
            height: 80%;
            width: 80%;
            fill: white;
            fill-opacity: 0.2; } }
    .background {
        position: relative;
        width: 40%;
        height: auto;
        margin: auto;
        h1 {
            letter-spacing: 1.5vw;
            font-size: 8vw; } }

    .sozialnav {
        padding-right: 2%;
        justify-content: flex-end;
        align-items: center;
        position: absolute;

        & ul {
            display: flex;
            height: 30%;
            justify-content: space-between;
            align-items: center;
            flex-direction: column; } }
    .swipercon {
        position: absolute;
        margin-top: 90vh;
        justify-content: center;
        align-items: flex-end;
        width: 100%; }

    .swiperLogo {
        position: relative;
        height: auto;
        width: 2.5rem; } }
